export function copyTextareaToClipboard(textareaSelector) {
    let textarea = document.querySelector(textareaSelector);
    textarea.select();
    document.execCommand("copy");
    window.getSelection().removeAllRanges();
}

export function multipleReplace(str, fromArray, toArray) {
    if (fromArray.length !== toArray.length) {
        console.log("Arrays of different length in multipleReplace");
        return;
    }
    let otherReg = "[^" +
        fromArray.join("") +
        "\\s\\n\\d\\.:<>\\{\\}\\(\\)#" +
        "]";
    otherReg = RegExp(otherReg, "g");
    str = str.replace( otherReg, "" );
    for (let i=0; i<fromArray.length; i++) {
        let from = fromArray[i].replace( /\s/g, "");
        let to = toArray[i].replace( /\s/g, "");
        let reg = RegExp( from, "g" );
        str = str.replace( reg, to )
    }
    return str;
}

export function inputToTop(textareaSelector) {
    const input = document.querySelector(textareaSelector);
    input.focus();
    input.scrollTop = 0;
    input.setSelectionRange(0,0);
}
