<template>
    <div class="container">
        <div class="action-buttons mb-7 d-flex justify-center">
            <v-btn v-for="button in actionButtons"
                   :key="button.text"
                   :color="button.color"
                   class="ml-1"
                   @click="doAction(button.value)"
                   depressed large tile>{{ button.text }}</v-btn>
        </div>
        <div class="textareas d-flex">
            <div class="mr-5">
                <v-btn depressed tile small outlined class="no-mouse">Source text</v-btn>
                <v-textarea no-resize
                            :background-color="textareaColors.source"
                            class="textarea-left textarea"
                            :class="!chosen.direction ? 'textarea-syriac': ''"
                            placeholder="Change the conversion direction if needed, type or paste the text and press Convert"
                            rows="15"
                            outlined
                            :value="inputText"
                            @input="onInput"
                ></v-textarea>
            </div>

            <div class="ml-5">
                <v-btn depressed tile small outlined class="no-mouse">Target text</v-btn>
                <v-textarea no-resize
                            :background-color="textareaColors.target"
                            class="textarea-right textarea"
                            :class="chosen.direction ? 'textarea-syriac': ''"
                            rows="15"
                            outlined
                            :value="targetText"
                ></v-textarea>
            </div>

        </div>


        <div class="switchers d-flex justify-space-around mb-7">
            <div class="switcher">
                <v-btn v-for="(name, idx) in directions"
                       :key="idx"
                       @click="toggle(idx, 'direction')"
                       depressed large
                       :outlined="idx === chosen.direction"
                       :text="idx !== chosen.direction"
                >{{ name }}
                </v-btn>
            </div>

            <div class="switcher">
                <v-btn v-for="(name, idx) in translitVariants"
                       :key="idx"
                       @click="toggle(idx, 'translitVariant')"
                       depressed large tile
                       :outlined="idx === chosen.translitVariant"
                       :text="idx !== chosen.translitVariant"
                >{{ name }}
                </v-btn>
            </div>
        </div>


        <div class="letters">
            <div class="wrapper" v-for="(tuple, i) in twoAlphabets" :key="i">
                <v-textarea v-for="(letter, idx) in tuple" rows="1"
                            cols="1"
                            class="mr-1 mb-1"
                            no-resize
                            :value="letter"
                            :key="letter"
                            :hide-details="true"
                            :background-color="idx ? textareaColors.target : textareaColors.source"
                            outlined
                            :maxlength="2"
                ></v-textarea>
            </div>
        </div>
    </div>
</template>

<script>
import _ from 'lodash';
import appBarTitleMixin from '@/mixins/appBarTitleMixin';
import { copyTextareaToClipboard, inputToTop, multipleReplace } from '@/services/transliterationHelpers';

const translit = "ʔbggdhYwzḥṭyklmnssʕppṣqrršt:";
const translit_ansi = "AbggdhYwzHTyklmnssCppSqrrWt:";
const originalLetters  = "ܐܒܓܔܕܗܞܘܙܚܛܝܟܠܡܢܣܤܥܦܧܨܩܪܖܫܬ̈";
const actionButtons = [
    { color: 'warning', text: 'Convert' },
    { color: 'info', text: 'Clear all' },
    { color: 'info', text: 'Defaults' },
    { color: 'success', text: 'Copy to clipboard' },
].map((button) => ({ ...button, value: _.camelCase(button.text) }))

function makeChosen() {
    return {
        direction: 0,
        translitVariant: 0,
    };
}

export default {
    name: "Transliteration",
    mixins: [appBarTitleMixin],
    data() {
        const chosen = makeChosen();
        return {
            appBarTitle: 'Syriac Transliteration',
            directions: Object.freeze(['From Syriac', 'To Syriac']),
            translitVariants: Object.freeze(['Unicode', 'Simple English']),
            chosen,
            translitArr: translit.split(''),
            translitAnsiArr: translit_ansi.split(''),
            originalLettersArr: originalLetters.split(''),
            actionButtons,
            inputText: '',
            targetText: '',
            textareaColors: Object.freeze({
                source: 'purple lighten-5',
                target: 'success lighten-5',
            })
        };
    },
    computed: {
        twoAlphabets() {
            const arrays = [
                this.originalLettersArr,
                this.chosen.translitVariant ? this.translitAnsiArr : this.translitArr,
            ];
            const fromArray = arrays[this.chosen.direction];
            const toArray = arrays[1 - this.chosen.direction];
            return fromArray.map((_, idx) => ([fromArray[idx], toArray[idx]]));
        },
        alphabetsForConversion() {
            return this.twoAlphabets.reduce((acc, tuple) => {
               return [
                   [...acc[0], tuple[0]],
                   [...acc[1], tuple[1]],
               ]
            }, [[],[]]);
        }
    },
    methods: {
        toggle(i, value) {
            this.chosen[value] = i;
        },
        convert() {
            console.log('converting');
            this.targetText = multipleReplace(this.inputText, ...this.alphabetsForConversion);
            const sourceTextareaSelector = '.textarea-left textarea';
            inputToTop(sourceTextareaSelector);
        },
        clearAll() {
            console.log('clearing all');
            this.inputText = '';
            this.targetText = '';
        },
        defaults() {
            console.log('making defaults');
            this.chosen = makeChosen();
            this.clearAll();
        },
        copyToClipboard() {
            console.log('copying to clipboard');
            const textareaSelector = '.textarea-right textarea';
            copyTextareaToClipboard(textareaSelector);
        },
        doAction(actionName) {
            const action = this[actionName];
            if (_.isFunction(action)) {
                action();
            }
        },
        onInput(text) {
            this.inputText = text;
        }
    },
}
</script>

